let elem = document.getElementById("searchBtn");
elem.onclick = function (e) {
    e.preventDefault();
    let search = document.querySelector(".header__search");

    search.classList.toggle("show");
}

let json =
    `[{
        "img":"544b0d4e05e6250aeba2.jpg",
        "name":"7 gold bags lari",
        "count":"490$"
    },
    {
        "img":"20f1b88aa53b93b4a4ce.jpg",
        "name":"white shirt dali",
        "count":"399$"
    },
    {
        "img":"4f70550e567e890d0aeb.jpg",
        "name":"shitra handbag",
        "count":"299$"
    }
]`;

document.addEventListener("DOMContentLoaded", function (event) {
    createPage();
});

function createPage() {
    let cards = JSON.parse(json);
    let cardContent = "";

    for (let card of cards) {
        cardContent +=
            `<div class="catalog__item catalog__hide">
            <div class="catalog__photo">
                <a href="./product.html" class="catalog__imgLink">
                    <div class="catalog__imgWrapper">
                        <img class="catalog__img" src="assets/${card.img}" alt="Rings">
                    </div>
                </a>
            </div>
            <div class="catalog__footer">
                <div class="catalog__description">
                    <a href="./product.html" class="catalog__link">
                        <p class="catalog__name">${card.name}</p>
                    </a>
                    <p class="catalog__price">${card.count}</p>
                </div>
                <div class="catalog__basketWrapper hidden">
                    <div class="catalog__basket">
                        <a href="" class="catalog__basketLink">Add to cart</a>
                        <img class="catalog__icon" src="assets/0c5c6c662057f5720e14.svg" alt="Cart">
                    </div>
                </div>
            </div>
        </div><br>`;
    }

    document.getElementById("searchContainer").innerHTML = cardContent;

    let url = new URL(window.location.href).searchParams.get("search");
    document.getElementById("searchInput").value = url;
    document.getElementById("SearchOnRequest").innerText = url;
    searchCard();

    function searchCard() {
        let searchInput = document.getElementById("searchInput").value;
        if (searchInput.trim().length == 0) {
            return;
        }
        let cards = document.querySelectorAll(".catalog__item");

        let count = 0;
        cards.forEach((card) => {
            let name = card.querySelector(".catalog__name");

            if (name && name.innerText.includes(searchInput.trim())) {
                card.classList.remove("catalog__hide");
                document.getElementById("SearchOnRequest").innerText = searchInput;
                count++;
            } else {
                card.classList.add("catalog__hide");
            }
        });
        if (count == 0) {
            document.getElementById("SearchOnRequest").innerText = "no items to show";
        }
    }

    document.querySelector(".header__searchBtn").addEventListener("click", (e) => {
        e.preventDefault();
        searchCard();
    });
}